.season-container {
  height: 100vh;
  width: 100vw;
  font-family: "Pangolin", cursive;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative;
  overflow-x: hidden;
  box-shadow: 1px 1px 15px #97CE4C;
  .season-heading{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    padding-top: 1rem;
  }
  
  @media (max-width: $desktop-breakpoint) {
    height: auto;
  }
}



.season-item-container {
  margin: 0;
  @media (max-width: $desktop-breakpoint) {
    margin: 4rem 0;
  }
}

.season-item {

  .card{
     height: 300px !important;
     width: 200px !important;
     transition:transform 400ms ease-in-out;

     &:hover{
         transform: scale(1.3);
     }
  }

  @media (max-width: $desktop-breakpoint) {
    display: flex;
    justify-content: center;
  }
}

