@import url(https://fonts.googleapis.com/css2?family=Pangolin&display=swap);
body{padding:0;margin:0;overflow-x:hidden;width:100vw;max-width:100%;background-color:black}h1,h2,h3,h4,h5,h6,p{padding:0;margin:0}h1{font-family:"Pangolin", cursive, sans-serif}.main-landing{height:100vh;width:100vw;background-image:url(/rick-and-morty-wiki/static/media/bg.4ea7fb83.png);background-size:cover;box-shadow:1px 1px 15px #97CE4C}@media (max-width: 45rem){.main-landing{background-position:center}}.title-container{display:flex;justify-content:center;padding:50px 0 0 0;width:100vw}.logo{height:200px;width:800px}@media (max-width: 45rem){.logo{height:100px;width:300px}}.heading-container{display:flex;justify-content:center;padding:10px 0 0 0}.texting{font-family:"Pangolin", cursive, sans-serif;color:white;font-size:35px}@media (max-width: 45rem){.texting{font-size:18px}}.season-container{height:100vh;width:100vw;font-family:"Pangolin", cursive;display:flex;flex-direction:row;justify-content:center;align-items:center;position:relative;overflow-x:hidden;box-shadow:1px 1px 15px #97CE4C}.season-container .season-heading{position:absolute;top:0;left:0;right:0;padding-top:1rem}@media (max-width: 45rem){.season-container{height:auto}}.season-item-container{margin:0}@media (max-width: 45rem){.season-item-container{margin:4rem 0}}.season-item .card{height:300px !important;width:200px !important;transition:transform 400ms ease-in-out}.season-item .card:hover{transform:scale(1.3)}@media (max-width: 45rem){.season-item{display:flex;justify-content:center}}.characters{display:flex;align-items:center;justify-content:center;height:100vh}@media (max-width: 45rem){.characters{height:280vh;padding:0 5vh}}.card{background-color:#262626;color:white;height:70vh;margin:0 20px 0 20px}@media (max-width: 45rem){.card{margin:5vh 0}}.card-img-top{height:50vh}.main-carousel{height:100vh;width:100vw;display:flex;justify-content:center;align-items:center;position:relative}.carousel-container{margin-top:1rem;width:100vw;height:100vh}.carousel-img{position:relative !important;-webkit-filter:blur(1.5px);filter:blur(1.5px);width:100vw;height:100vh}.carousel-caption{position:absolute !important;top:0;bottom:0;left:0;right:0;display:flex !important;justify-content:center;align-items:center;z-index:0}

