.main-carousel {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;


}



.carousel-container {
  margin-top: 1rem;
  width: 100vw;
  height: 100vh;

}

.carousel-img {
  position: relative !important;
  filter: blur(1.5px);
  width: 100vw;
  height: 100vh;
}



.carousel-caption {
  position: absolute !important;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex !important;
  justify-content: center;
  align-items: center;
  z-index: 0;
}
